import React, { useEffect, useState } from 'react';
import ColorThief from 'colorthief';
import Color from 'color';

// Dynamically import all images from the public/images folder
const importAll = (r) => r.keys().map(r);
const images = importAll(require.context('../../public/images', false, /\.(png|jpe?g|svg)$/));

const DynamicImageDisplay = () => {
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  const [windowHeight, setWindowHeight] = useState(window.innerHeight);
  const [sortedImages, setSortedImages] = useState([]);
  const [fullScreenImage, setFullScreenImage] = useState(null);

  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
      setWindowHeight(window.innerHeight);
    };

    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  useEffect(() => {
    const colorThief = new ColorThief();
    const imageElements = [];
    let loadedImages = 0;

    const loadImageColors = () => {
      const imageColors = [];

      imageElements.forEach((img, index) => {
        const dominantColor = colorThief.getColor(img);
        const color = Color.rgb(dominantColor).hsl().color; // Convert to HSL
        imageColors.push({ src: images[index], color });
      });

      // Sort by hue, then saturation, then lightness
      imageColors.sort((a, b) => {
        const [hueA, saturationA, lightnessA] = a.color;
        const [hueB, saturationB, lightnessB] = b.color;
        if (hueA !== hueB) return hueA - hueB;
        if (saturationA !== saturationB) return saturationA - saturationB;
        return lightnessA - lightnessB;
      });

      setSortedImages(imageColors);
    };

    images.forEach((src, index) => {
      const img = new Image();
      img.src = src;
      img.onload = () => {
        imageElements[index] = img;
        loadedImages += 1;
        if (loadedImages === images.length) {
          loadImageColors();
        }
      };
    });
  }, [images]);

  const handleImageClick = (src) => {
    setFullScreenImage(src);
  };

  const handleClose = (e) => {
    // Close if the click is outside the full-screen image
    if (e.target.classList.contains('overlay')) {
      setFullScreenImage(null);
    }
  };

  return (
    <div style={{ position: 'relative' }}>
      <div className={fullScreenImage ? 'blurred' : ''} style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center' }}>
        {sortedImages.map((image, index) => (
          <img
            key={index}
            src={image.src}
            alt={`img-${index}`}
            className="dynamic-image"
            style={{
              maxWidth: `${windowWidth / 3}px`,
              maxHeight: `${windowHeight / 3}px`,
              margin: '10px',
              cursor: 'pointer',
            }}
            onClick={() => handleImageClick(image.src)}
          />
        ))}
      </div>
      {fullScreenImage && (
        <div className="overlay visible" onClick={handleClose}>
          <img src={fullScreenImage} alt="Full Screen" className="full-screen-image" />
        </div>
      )}
    </div>
  );
};

export default DynamicImageDisplay;
